.ViewGigRequest .carousel .thumbs-wrapper {
  text-align: left; }

.ViewGigRequest .carousel.carousel-slider .control-arrow {
  background: none !important; }

.ViewGigRequest .carousel .thumb {
  border: none; }

.ViewGigRequest .carousel .thumbs {
  padding: 0;
  margin: 0; }

.ViewGigRequest .carousel .slide img {
  width: auto;
  vertical-align: top;
  border: 0;
  max-height: 300px; }

.ViewGigRequest .carousel.carousel-slider .control-arrow,
.ViewGigRequest .carousel .control-arrow,
.ViewGigRequest .carousel .control-next.control-arrow:before {
  background: #fff;
  opacity: 1 !important; }

.ViewGigRequest .carousel .thumbs-wrapper {
  margin: 0px !important; }

.ViewGigRequest .carousel .thumb.selected,
.ViewGigRequest .carousel .thumb:hover {
  border: 1px solid #333 !important; }

.ViewGigRequest .carousel.carousel-slider .control-arrow {
  top: 45% !important;
  height: 44px;
  background-color: #fff !important;
  padding: 0px !important; }

.ViewGigRequest .carousel .control-next.control-arrow:before {
  content: "\f061" !important;
  font-family: FontAwesome;
  color: #000 !important; }

.ViewGigRequest .carousel .control-prev.control-arrow:before {
  content: "\f060" !important;
  font-family: FontAwesome;
  color: #000 !important; }

.ViewGigRequest .carousel .control-arrow:before,
.ViewGigRequest .carousel.carousel-slider .control-arrow:before {
  border-top: none !important;
  border-bottom: none !important; }

.ViewGigRequest .bid-row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between; }
  .ViewGigRequest .bid-row .bid-thumb {
    max-width: 150px;
    margin-right: 20px; }
    .ViewGigRequest .bid-row .bid-thumb img {
      max-width: 100%; }
  .ViewGigRequest .bid-row .bid-desc {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .ViewGigRequest .bid-row .bid-desc label {
      margin: 0;
      font-size: 12px;
      color: #666; }
    .ViewGigRequest .bid-row .bid-desc .title {
      font-size: 20px;
      font-weight: bold; }
    .ViewGigRequest .bid-row .bid-desc .price {
      color: #5931ea;
      font-weight: bold;
      margin-bottom: 10px; }
